<template>
  <div>
    <loading-spinner v-if="loading"></loading-spinner>
    <div class="page-container" v-else>
      <!-- <printHeader /> -->
      <clientData />
      <projectData />
      <roomData />
      <measurementData />
      <!-- <analysisAdviceData /> -->
    </div>
  </div>
</template>

<script>
// import printHeader from "./client-page-components/print-header.vue";
import clientData from "./client-page-components/client-data";
import projectData from "./client-page-components/project-data";
import roomData from "./client-page-components/room-data";
import measurementData from "./client-page-components/measurement-data";
// import analysisAdviceData from "./client-page-components/analysis-advice-data";
import LoadingSpinner from "./LoaderSpinner.vue";

import Firebase from "firebase";

export default {
  name: "Measurement",
  components: {
    // printHeader,
    clientData,
    projectData,
    roomData,
    measurementData,
    LoadingSpinner
    // analysisAdviceData
  },
  data() {
    return {
      loading: true,
      currentProject: null,
      currentClient: null,
      currentRoom: null,
      currentMeasurement: null
    };
  },
  computed: {
    currentMeasurementDisplayName() {
      let displayName = this.currentMeasurement.data().type;
      if (this.currentMeasurement.data().label.length > 0)
        displayName += `, ${this.currentMeasurement.data().label}`;
      return displayName;
    }
  },
  methods: {
    openEditModal() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "edit",
        shown: true
      });
    }
  },
  watch: {
    currentClient() {
      this.$store.commit("setCurrentClientPageClient", this.currentClient);
    },
    currentProject() {
      this.$store.commit("setCurrentClientPageProject", this.currentProject);
    },
    currentRoom() {
      this.$store.commit("setCurrentClientPageRoom", this.currentRoom);
    },
    currentMeasurement() {
      this.$store.commit(
        "setCurrentClientPageMeasurement",
        this.currentMeasurement
      );
    }
  },
  mounted() {
    Firebase.firestore()
      .collection("measurements")
      .doc(this.$route.params.measurementId)
      .get()
      .then(doc => {
        this.currentMeasurement = doc;
        this.$setPageTitle(`Meting - ${this.currentMeasurementDisplayName}`);
        return doc;
      })
      .then(measurement => {
        Firebase.firestore()
          .collection("rooms")
          .doc(measurement.data().belongsTo)
          .get()
          .then(doc => {
            this.currentRoom = doc;
            return doc;
          })
          .then(room => {
            Firebase.firestore()
              .collection("projects")
              .doc(room.data().belongsTo)
              .get()
              .then(doc => {
                this.currentProject = doc;
                return doc;
              })
              .then(project => {
                Firebase.firestore()
                  .collection("clients")
                  .doc(project.data().belongsTo)
                  .get()
                  .then(doc => {
                    this.currentClient = doc;
                    this.$store.commit("setCurrentClientPage", {
                      client: this.currentClient,
                      project: this.currentProject,
                      room: this.currentRoom,
                      measurement: this.currentMeasurement
                    });
                    this.loading = false;
                  });
              });
          });
      });
  },
  destroyed() {
    this.$store.commit("setCurrentClientPage", {});
  }
};
</script>



