<template>
  <div
    class="page-box"
    v-if="currentMeasurement"
  >
    <div class="page-box-title">
      <h3>
        <div class="label">Meting</div>
        {{currentMeasurementDisplayName}}
      </h3>
      <div class="flex-divider"></div>
      <div class="page-box-title-buttons">
        <base-button
          type="secondary"
          class="small-on-mobile"
          @click="openAnalysis"
          v-show="currentMeasurement.data().type == 'Nulmeting'"
        >
          <base-icon
            iconRef="clipboard"
            position="left"
          /><span class="button-label">Advies opstellen</span>
        </base-button>
        <base-button
          type="secondary"
          @click="openEditModal"
        >
          <base-icon iconRef="edit" />
        </base-button>
        <base-button
          type="secondary"
          @click="closeCurrentMeasurement"
        >
          <base-icon iconRef="close" />
        </base-button>
      </div>
    </div>
    <div class="page-box-content values">
      <div
        class="value-group"
        v-if="currentMeasurement.data().description != ''"
      >
        <div class="value">{{currentMeasurement.data().description}}</div>
      </div>
      <div class="value-row">
        <div class="value-group">
          <div class="label">Datum</div>
          <div class="value">{{currentMeasurementDatePretty}}</div>
        </div>
        <div class="value-group">
          <div class="label">Meting door</div>
          <div class="value">{{currentMeasurement.data().author}}</div>
        </div>
        <div class="value-group">
          <div class="label">125 Hz</div>
          <div class="value">{{currentMeasurement.data().hz125 | $twoDecimals}} s</div>
        </div>
        <div class="value-group">
          <div class="label">250 Hz</div>
          <div class="value">{{currentMeasurement.data().hz250 | $twoDecimals}} s</div>
        </div>
        <div class="value-group">
          <div class="label">500 Hz</div>
          <div class="value">{{currentMeasurement.data().hz500 | $twoDecimals}} s</div>
        </div>
        <div class="value-group">
          <div class="label">1000 Hz</div>
          <div class="value">{{currentMeasurement.data().hz1000 | $twoDecimals}} s</div>
        </div>
        <div class="value-group">
          <div class="label">2000 Hz</div>
          <div class="value">{{currentMeasurement.data().hz2000 | $twoDecimals}} s</div>
        </div>
        <div class="value-group">
          <div class="label">4000 Hz</div>
          <div class="value">{{currentMeasurement.data().hz4000 | $twoDecimals}} s</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    currentMeasurement: function() {
      return this.$store.getters.getCurrentClientPage.measurement;
    },
    currentMeasurementDisplayName: function() {
      let displayName = this.currentMeasurement.data().type;
      if (this.currentMeasurement.data().label.length > 0)
        displayName += `, ${this.currentMeasurement.data().label}`;
      return displayName;
    },
    currentMeasurementDatePretty: function() {
      return this.$prettifyDate(this.currentMeasurement.data().date.seconds);
    }
  },
  methods: {
    openAnalysis: function() {
      this.$router.push(`/analyse-en-advies/${this.currentMeasurement.id}`);
    },
    closeCurrentMeasurement: function() {
      this.$router.push(`/ruimte/${this.currentMeasurement.data().belongsTo}`);
    },
    openEditModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "measurement",
        modalType: "edit",
        shown: true
      });
    }
  }
};
</script>


